* {
      margin: 0;
      padding: 0;
      color: white;
}

.bg {
      background-color: #171717;
      position: fixed;
      width: 100vw;
      height: 100vh;
      overscroll-behavior: none;
}

.circle {
      width: 400px;
      height: 400px;
      position: absolute;
      bottom: 0;
      right: 0;
      border-radius: 100% 0% 0% 100% / 100% 0% 100% 0%;
}

.circle1 {
      width: 300px;
      height: 300px;
      position: absolute;
      top: 0;
      left: 0;
      border-radius: 0% 100% 100% 0% / 100% 0% 100% 0%;
}

.container {
      position: absolute;
      width: 87vw;
      height: 80vh;
      top: 10%;
      left: 6%;
      background-color: #171717;
      box-shadow: 0 3px 10px rgb(0 0 0);
      overflow-y: auto;
}

.container::-webkit-scrollbar {
      display: none;
}

.container {
      -ms-overflow-style: none;
      /* IE and Edge */
      scrollbar-width: none;
      /* Firefox */
}

