.experiencemagic {
      background-image: url("../images/experiencemagic.webp");
      width: 100%;
      height: 150px;
      background-size: contain;
      background-repeat: no-repeat;
      opacity: 1;
      transform: translateZ(0) scale(1);
      transition-timing-function: ease;
      transition-duration: 1.2s;
      transition-property: opacity,transform;
}

.Button-bg
{
      width: 100px;
      height: 40px;
      border-radius: 10px;
      position: absolute;
      right: 200px;
      top: 10px;
      align-items: center;
      text-align: center;
      font-size: 20px;
      justify-content: center;
}

@keyframes animate {
      0% {
            top: 10%;
      }

      50% {
            top: 12%;
      }

      100% {
            top: 10%;
      }
}

