@import url('https://fonts.googleapis.com/css2?family=EB+Garamond&family=Raleway:wght@200&family=Tiro+Gurmukhi&display=swap');

.modes {
      position: absolute;
      right: 20px;
      top: 15px;
      width: 140px;
      color: white;
      font-size: 30px;
      display: flex;
      justify-content: space-around;
}

.default {
      height: 20px;
      width: 20px;
      background-color: rgb(240, 235, 167);
      border-radius: 50%;
}

.green {
      height: 20px;
      width: 20px;
      background-color: #556b2f;
      border-radius: 50%;
}

